%custom-container {
    max-width: 1400px;
    width: 100%;
    margin: 0 auto;
    padding: 0 20px;

    @media screen and (max-width: 1490px) {
        max-width: 1079px;
    }

    @media screen and (max-width: 1440px) {
        // max-width: 1110px;
    }

    @media screen and (max-width: 1200px) {
        max-width: 1000px;
    }

    @media screen and (max-width: 992px) {
        max-width: 700px;
    }

    @media screen and (max-width: 768px) {
        width: 100%;
        padding: 0 20px;
    }
}

.custom-container {
    @extend %custom-container;

    .custom-container {
        width: 100%;
        padding: 0;
    }

    &.reviews {
        max-width: 1250px;

        @media screen and (max-width: 1440px) {
            max-width: 1110px;
        }
    }

    .slider-popup & {
        max-width: 1250px;

        @media screen and (max-width: 991px) {
            @media screen and (max-width: 992px) {
                max-width: 700px;
            }

            @media screen and (max-width: 768px) {
                width: 100%;
                padding: 0 20px;
            }
        }
    }

    .browse-topic__filters-wrap .custom-container {
        @media screen and (max-width: 1350px) {
            width: 1100px;
        }
        @media screen and (max-width: 1200px) {
            width: 1000px;
        }

        @media screen and (max-width: 992px) {
            width: 700px;
        }

        @media screen and (max-width: 768px) {
            width: 100%;
            padding: 0 20px;
        }
    }

    .wp-block-columns {
        @media screen and (max-width: 768px) {
            flex-direction: column-reverse;
        }

        .wp-block-column {
            p,
            img {
                //@media screen and (max-width: 768px) {
                //    width: 100%;
                //}
            }

            p {
                @media screen and (max-width: 768px) {
                    font-size: 18px;
                    font-weight: 300;
                    line-height: 1;
                }
            }

            img {
                @media screen and (max-width: 480px) {
                    height: 195px;
                    object-fit: cover;
                }
            }
        }
    }

    .custom-content {
        padding-top: 0;
        padding-bottom: 0;

        &__inner {
            .custom-container {
                @media screen and (max-width: 480px) {
                    padding-left: 0;
                    padding-right: 0;
                }
            }
        }

        &__text {
            h1,
            h2,
            h3,
            h4,
            h5,
            h6 {
                color: #17181A;
            }
        }
    }

    ul {
        font-family: Turbine, sans-serif;

        li {
            color: #46494d;
        }
    }
}

.slider-popup.content-vertical .custom-container {
