button {
    @extend %button;
}

.orange-button {
    @extend %orange-button;
}


.transparent-orange-btn,
.orange-transparent-btn,
.transparent-orange-dark-text-btn {
    @extend %button;
    display: inline-block;
    color: #F3F0EC;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Turbine, sans-serif;
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: 1.25;
    margin: 0;
    text-decoration: none;
    position: relative;
    padding: 0 8px;
    border: 2px solid #CC7510;
    transition: all 0.3s ease-out;
    text-transform: uppercase;

    @media screen and (max-width: 480px) {
        font-size: 16px;
        border-radius: 4px;
        padding: 7px 26px;
    }

    &:hover {
        @media screen and (min-width: 768px) {
            background-color: #CC7510;
        }
    }
}

.transparent-orange-dark-text-btn {
    color: #000;

    &:hover {
        color: #F3F0EC;
    }
}

.orange-transparent-btn {
    background-color: #CC7510;

    &:hover {
        background-color: transparent;
        color: #CC7510;
    }
}

.blue-btn {
    @extend %button;
    font-feature-settings: "clig" off, "liga" off;
    background-color: #5b8cab;
    color: var(--White-Text, #f3f0ec);
    display: flex;
    align-items: center;
    justify-content: center;
    width: max-content;
    font-family: Turbine, sans-serif;
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: 1.25;
    padding: 0 24px;
    text-decoration: none;
    transition: all .3s;
    text-transform: uppercase;

    @media screen and (max-width: 670px) {
        padding-left: 14px;
        padding-right: 14px;
    }
}

.orange-btn {
    @extend %button;
    appearance: none;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 0 8px;
    border-radius: 0;
    border: 1px solid var(--Orange, #cc7510);
    color: #fff;
    font-family: Turbine, sans-serif;
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: 1.25;
    cursor: pointer;
    background-color: #cc7510;
    text-decoration: none;
}

.large-btn {
    font-size: 60px;
    line-height: 1.45;
    padding-left: 44px;
    padding-right: 44px;
    border-width: 5px;
    border-radius: 6px;

    @media screen and (max-width: 1024px) {
        font-size: 50px;
        padding-left: 24px;
        padding-right: 24px;
    }

    @media screen and (max-width: 480px) {
        border-width: 2px;
        border-radius: 2px;
        font-size: 16px;
        padding-left: 16px;
        padding-right: 16px;
    }
}

.medium-btn {
    font-size: 24px;
    line-height: 1.45;
    border-width: 2px;
    border-radius: 2px;

    @media screen and (max-width: 1024px) {
        font-size: 20px;
    }

    @media screen and (max-width: 480px) {
        font-size: 16px;
    }
}

.small-btn {
    font-size: 18px;
    line-height: 1.25;
    border-width: 2px;
    border-radius: 2px;

    @media screen and (max-width: 480px) {
        font-size: 16px;
