.blink-animation {
    animation: blink 1s infinite alternate-reverse;
}

@keyframes blink {
    from {
        opacity: 1;
        transform: scale(1, 1);
    }

    to {
        opacity: 0.5;
        transform: scale(1.1, 1.1);
    }
}


.animated-element {
    opacity: 0;
}

.from-left {
    &.is-visible {
        animation: 1s forwards linear ease-in-out;
        animation-name: from-left;
    }
}

.from-right {
    &.is-visible {
        animation: 1s forwards linear ease-in-out;
        animation-name: from-right;
    }
}

.custom-fade {
    &.is-visible {
        animation: 2s forwards linear ease-in-out;
        animation-name: custom-fade;
    }
}

@keyframes from-right {
    from {
        opacity: 0;
        transform: translateX(100%);
    }

    to {
        opacity: 1;
        transform: translateX(0);
    }
}

@keyframes from-left {
    from {
        opacity: 0;
        transform: translateX(-100%);
    }

    to {
        opacity: 1;
        transform: translateX(0);
    }
}

@keyframes custom-fade {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

textarea,
.wpcf7-select,
input[type="text"],
input[type="tel"],
input[type="email"] {
    width: 100%;
    padding: 10px 16px;
    color: #757981;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Turbine, sans-serif;
    font-size: 30px;
    font-style: normal;
    font-weight: 300;
    line-height: 1.2;
    appearance: none;

    border-radius: 5px;
    border: 1px solid #757981;
    background-color: #fff;
    resize: none;

    @media screen and (max-width: 768px) {
        font-size: 24px;
    }

    @media screen and (max-width: 670px) {
        font-size: 16px;
    }

    @media screen and (max-width: 480px) {
        font-size: 14px;
    }

    &:focus {
        outline-color: #151836
    }
}

.wpcf7-select {
    text-overflow: ellipsis;
    padding: 10px 40px 10px 10px;
    background-repeat: no-repeat;
    background-position: 99% center;
    background-size: 28px 28px;
    background-image: url(data:image/svg+xml;base64,);

    @media screen and (max-width: 670px) {
        background-size: 22px 22px;
        background-position: 97% center;
    }
}

.checkbox {
    position: relative;
    padding-left: 22px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;

    input {
        cursor: pointer;
        position: absolute;
        width: 100%;
        height: 100%;
        opacity: 0;
        top: 0;
        left: 0;
        z-index: 1;
    }

    .text {
        color: #1D1D1B;
        font-family: Turbine, sans-serif;
        font-size: 1.125rem;
        font-style: normal;
        font-weight: 500;
        line-height: 1;
        margin: 0;
    }

    .icon {
        position: absolute;
        top: 0;
        left: 0;
        width: 16px;
        height: 16px;
        border: 3px solid #CC7510;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
    }

    input:checked + .icon {
        background-image: url(data:image/svg+xml;base64,);
    }
}

.aligncenter {
    clear: both;
    text-align: center;
    float: none;
    margin: 0 auto;
    display: block;
}

.alignleft {
    float: left;
    margin-right: 2rem;
    margin-bottom: 2rem;

    @media screen and (max-width: 768px) {
        float: none;
        margin: 0 0 1rem;
    }
}

.alignright {
    float: right;
    margin-left: 2rem;
    margin-bottom: 2rem;

    @media screen and (max-width: 768px) {
        float: none;
        margin: 0 0 1rem;
    }
}

body.single-resources {
    background: #F3F0EC;
}

body #pojo-a11y-toolbar {
    top: auto !important;
    bottom: -250px !important;

    @media screen and (max-width: 768px) {
        bottom: -200px !important;
    }

    @media screen and (max-width: 670px) {
        bottom: -128px !important;
    }
}


/*Base flex*/
.d-flex {
    display: flex;
}

.d-inline-flex {
    display: inline-flex;
}

.flex-fill {
    flex: 1 1 auto;
}

/*Flex direction*/
.flex-row {
    flex-direction: row;
}

.flex-row-reverse {
    flex-direction: row-reverse;
}

.flex-column {
    flex-direction: column;
}

.flex-row-reverse {
    flex-direction: column-reverse;
}


/*Flex justify content*/
.justify-content-start {
    justify-content: flex-start;
}

.justify-content-end {
    justify-content: flex-end;
}

.justify-content-center {
    justify-content: center;
}

.justify-content-between {
    justify-content: space-between;
}

.justify-content-around {
    justify-content: space-around;
}


/*Flex align items*/
.align-items-start {
